import React from 'react';
import _ from 'lodash';
import { Link } from 'react-router-dom';
import navPropTypes from '../../data/prop-types/nav';
import { HOME } from '../../data/constants/intralinks';

const DesktopNav = ({ nav, location, ...props } = props) => {
  return (
    <div className="nav desktop hidden-md">
      <div className="logo-container">
        <Link to={HOME}>
          <img
            className="logo"
            src={require('../../images/UCG_Black.png')}
            alt="The logo for Unlucky Cricket Games"
          />
        </Link>
      </div>
      <nav>
        {_.map(nav, navData => (
          <Link
            id={navData.name}
            key={navData.name}
            className={`nav-link${
              location.pathname.includes(navData.link) ? ' active' : ''
            }`}
            to={navData.link}
          >
            {navData.description.toUpperCase()}
          </Link>
        ))}
      </nav>
    </div>
  );
};

DesktopNav.propTypes = {
  nav: navPropTypes
};

export default DesktopNav;
