import React from 'react';
import { Link } from 'react-router-dom';
import Heading from './Heading';
import Text from './Text';
import Card from './Card';
import { HOME } from '../../data/constants/intralinks';

/**
 * The page that's shown when the user tries to navigate to a URL that's not recognized by the router.
 *
 * @param {object} props
 */

// TODO: update [link] to use attribute
const NotFoundPage = props => {
  return (
    <Card center className="responsive-full-width">
      <Heading importance="1" className="text-center">
        Uh oh
      </Heading>
      <Text className="text-center">
        It looks like what you were looking for isn't where you thought you'd
        find it. But, have no fear, intrepid internaut. We've provided a link to
        our <Link to={HOME}>Home Page</Link> so you can get back to
        civilization.
      </Text>
    </Card>
  );
};

export default NotFoundPage;
