/**
 * @returns {string} the UUID as a string.
 */
export const getUuid = () => {
  var dt = new Date().getTime();
  var uuid = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(
    c
  ) {
    var r = (dt + Math.random() * 16) % 16 | 0;
    dt = Math.floor(dt / 16);
    return (c == 'x' ? r : (r & 0x3) | 0x8).toString(16);
  });
  return uuid;
};

/**
 * Takes a number and returns just the integer of the number. Technically doesn't
 * round, but you can think of it as truncation.
 *
 * @param {Number} num the number to strip of a floating point.
 * @return {Number} the integer part of the number.
 */
export const getInteger = num => {
  return num - (num % 1);
};
