import React from 'react';
import InfoCard from '../common/InfoCard';
import InfoCardSection from '../common/InfoCardSection';
import Text from '../common/Text';
import LinkButton from '../common/LinkButton';
import CardGrid from '../common/CardGrid';
import ScreenshotCard from '../common/ScreenshotCard';
import SupportedDevicesList from '../common/SupportedDevicesList';
import { USE_CLOCKULATOR } from '../../data/constants/intralinks';

const Clockulator = ({ ...props } = props) => {
  return (
    <InfoCard
      className="info-clockulator"
      title="The Clockulator"
      imgSrc={require('../../images/software/clockulator/clockulator-screenshot0.jpg')}
      imgAlt="A screenshot of The Clockulator"
      imgXOffset="-0.5rem"
      imgYOffset="20rem"
    >
      <SupportedDevicesList icons={['globe']} />
      <InfoCardSection title="Details">
        <Text>
          The Clockulator is an app that runs in your browser, so unlike a lot
          of our software, there's no need to download it! The Clockulator
          allows you to enter the time of your earliest obligation and things
          you have to do before that. You give it the amount of time you'll
          spend on each of those things, and it'll tell you how late you can
          sleep in! A nice tool for planning out a complicated day while making
          sure you get the sleep you need.
        </Text>
        <Text>The Clockulator was created by Joshua Truitt.</Text>
      </InfoCardSection>
      <InfoCardSection title="Screenshots" noIndent>
        <CardGrid>
          <ScreenshotCard
            imageProps={{
              src: require('../../images/software/clockulator/clockulator-screenshot0.jpg')
            }}
          />
        </CardGrid>
      </InfoCardSection>
      <InfoCardSection className="hidden-sm" title="Resources">
        <LinkButton to={USE_CLOCKULATOR} name="The Clockulator" />
      </InfoCardSection>
    </InfoCard>
  );
};

export default Clockulator;
