import _ from 'lodash';
import React from 'react';
import InfoCard from '../common/InfoCard';
import InfoCardSection from '../common/InfoCardSection';
import Text from '../common/Text';
import DownloadButton from '../common/DownloadButton';
import CardGrid from '../common/CardGrid';
import ScreenshotCard from '../common/ScreenshotCard';
import SupportedDevicesList from '../common/SupportedDevicesList';
import { getUuid } from '../../data/util';
import LinkButton from '../common/LinkButton';

const PopCube = ({ ...props } = props) => {
  const screenshots = [
    require('../../images/games/pop-cube/pop-cube-screenshot0.jpg'),
    require('../../images/games/pop-cube/pop-cube-screenshot1.jpg'),
    require('../../images/games/pop-cube/pop-cube-screenshot2.jpg')
  ];

  return (
    <InfoCard
      className="info-pop-cube"
      title="Pop! Cube"
      imgSrc={require('../../images/games/pop-cube/pop-cube-screenshot1.jpg')}
      imgAlt="A screenshot of Pop! Cube"
      imgXOffset="-43.5rem"
      imgYOffset="24rem"
    >
      <SupportedDevicesList icons={['mobile-alt']} />
      <InfoCardSection title="Details">
        <Text>
          Pop! Cube is an arcade game all about popping various cube with your
          finger! Cubes get thrown from the bottom of the screen, and your job
          is to boop some of those little buggers while avoiding other ones.
          Make choices based on color-coding, and pop all the good ones and let
          all the bad ones go! Watch out for special cubes, though, like the
          black cube that reduces your health to 1 if you let it fall, or gold
          cubes that fully restore your health! Play endlessly with randomly
          generated play that speeds up as you go, play the way you want with
          several difficulties (including one meant for kids where you can't
          lose), and compare your highscores to those of friends!
        </Text>
        <Text>How far can you get?</Text>
      </InfoCardSection>
      <InfoCardSection title="Screenshots" noIndent>
        <CardGrid>
          {_.map(screenshots, screenshot => (
            <ScreenshotCard imageProps={{ src: screenshot }} key={getUuid()} />
          ))}
        </CardGrid>
      </InfoCardSection>
      <InfoCardSection title="Resources">
        <ul className="horizontal">
          <li>
            <LinkButton
              to="https://play.google.com/store/apps/details?id=com.UnluckyCricketGames.ShootTheCube"
              name="Pop! Cube on Google Play"
            />
          </li>
          <li>
            <LinkButton
              to="https://ucg-download.github.io/files/popcube_changelog.txt"
              name="Changelog"
            />
          </li>
        </ul>
      </InfoCardSection>
    </InfoCard>
  );
};

export default PopCube;
