import React from 'react';
import _ from 'lodash';

/**
 * Organizes a series of cards into a grid that resizes automatically. It's intended that the children of this
 * element will be FlexGridItems.
 */
const FlexGrid = ({ className, children, ...props } = props) => (
  <div className={`flex-grid${className ? ` ${className}` : ''}`}>
    {children}
  </div>
);

export default FlexGrid;
