import React from 'react';
import PropTypes from 'prop-types';
import Heading from '../common/Heading';
import Image from '../common/Image';
import Text from '../common/Text';
import Card from '../common/Card';
import HorizontalRule from '../common/HorizontalRule';

const featureImgStyle = {
  maxHeight: 1080,
  overflow: 'hidden'
};

const featureTextHeadingStyle = {
  paddingBottom: 0,
  marginBottom: 0
};

const featureTextCardStyle = {
  padding: '2rem',
  zIndex: 1
};

/**
 * Represents a featured item. A featured item consists of a full-width image,
 * a title representing what the feature is about, and then takes text and/or JSX as children.
 * The children represent what goes in the body of the feature, which is
 * more details on the feature, like a short description, perhaps including a link.
 */
const Feature = ({ imgSrc, imgAlt, title, ...props } = props) => {
  return (
    <Card className="feature" fillWidth>
      <Image fillWidth style={featureImgStyle} src={imgSrc} alt={imgAlt} />
      <Card fillWidth style={featureTextCardStyle}>
        <div style={{ width: '80%', margin: 'auto' }}>
          <Heading
            className="text-center text-caps"
            importance="4"
            style={featureTextHeadingStyle}
          >
            {title}
          </Heading>
          <HorizontalRule uncover />
          <Text className="text-center">{props.children}</Text>
        </div>
      </Card>
    </Card>
  );
};

Feature.propTypes = {
  /**
   * The src for the feature's image.
   */
  imgSrc: PropTypes.string.isRequired,
  /**
   * The alt for the feature's image.
   */
  imgAlt: PropTypes.string,
  /**
   * The title of the feature. For example, GAMES
   */
  title: PropTypes.string.isRequired,
  /**
   * The children that you want to show in the text of the card. Likely just
   * plain text, but you can include JSX elements as well.
   */
  children: PropTypes.array
};

export default Feature;
