import React from 'react';
import InfoCard from '../common/InfoCard';
import InfoCardSection from '../common/InfoCardSection';
import Text from '../common/Text';
import DownloadButton from '../common/DownloadButton';
import CardGrid from '../common/CardGrid';
import ScreenshotCard from '../common/ScreenshotCard';
import SupportedDevicesList from '../common/SupportedDevicesList';

const Virtuoso = ({ ...props } = props) => {
  return (
    <InfoCard
      className="info-virtuoso"
      title="Virtuoso"
      imgSrc={require('../../images/software/virtuoso/virtuoso-icon.ico')}
      imgAlt="A screenshot of Virtuoso"
      imgXOffset="-3rem"
      imgYOffset="2rem"
    >
      <SupportedDevicesList icons={['desktop']} />
      <InfoCardSection title="Details">
        <Text>
          Are you an indie game developer or music enthusiast? Well, we are, and
          we were sick and tired of never being able to make our music exactly
          how we wanted it. Instead, we had to rely on libraries of loops. While
          they may be expansive, sifting through hundreds of audio files just to
          find the progression that sounds as close as possible to the thing you
          want is silly. Why couldn't we just feed a program the notes we want
          and how long to play them, and have it spit out nice audio files for
          us? Well, that's what Virtuoso was built to do. With Virtuoso, you can
          type up a text file similar to the one pictured below (which is being
          displayed in <a href="https://atom.io/">Atom</a>) and then just tell
          Virtuoso to create it. Virtuoso will create pure tones (sorry, you
          can't make a saxophone sound) that match the frequencies of the notes
          you specify. You can also specify the speed of playback, tracks and
          measures, durations of the notes, their octaves, and chords.
        </Text>
        <Text className="hidden-lg">
          With Virtuoso, you can make all sorts of neat things! We even
          transcribed the song <span className="text-italic">Still Alive</span>{' '}
          from the popular game{' '}
          <a href="http://store.steampowered.com/app/400/Portal/">Portal</a>.
          You can listen to the song using the music widget on the page.
        </Text>
        <Text>
          You command Virtuoso to compile your text files into music via a
          command line window. This can seem intimidating at first if you're not
          used to using a command line, but we've got you covered. Virtuoso uses
          its own command line so there's no need to learn any fancy
          programmery. Virtuoso only has a small set of commands to get its job
          done, and you can get a list of all of them but typing "help" (no
          quotes) and hitting enter in the command window that comes up when you
          run Virtuoso. To get a running start on learning how to use Virtuoso,
          download the zip archive with the download button down there, extract
          the files, run Virtuoso, and then type, "open example" (no quotes),
          then hit Enter. That will open up an example song specifically written
          to help you learn. Now, go make your music!
        </Text>
        <Text>Virtuoso was created by Joshua Truitt.</Text>
      </InfoCardSection>
      <InfoCardSection title="Screenshots" noIndent>
        <CardGrid>
          <ScreenshotCard
            imageProps={{
              src: require('../../images/software/virtuoso/virtuoso-screenshot0.jpg')
            }}
          />
        </CardGrid>
      </InfoCardSection>
      <InfoCardSection className="hidden-sm" title="Resources">
        <DownloadButton
          src="https://ucg-download.github.io/files/Virtuoso.zip"
          name="Virtuoso"
        />
      </InfoCardSection>
    </InfoCard>
  );
};

export default Virtuoso;
