import React from 'react';
import PropTypes from 'prop-types';
import _ from 'lodash';

/**
 * Organizes a series of cards into a grid that resizes automatically. It's intended that the children of this
 * element will be CardGridItems.
 */
const CardGrid = ({ className, children, ...props } = props) => (
  <div className={`card-grid${className ? ` ${className}` : ''}`}>
    {children}
  </div>
);

CardGrid.propTypes = {};

export default CardGrid;
