import PropTypes from 'prop-types';

const baseSectionShape = {
  name: PropTypes.string.isRequired,
  description: PropTypes.string.isRequired,
  link: PropTypes.string.isRequired
};

const sectionShape = {
  ...baseSectionShape,
  subSections: PropTypes.arrayOf(PropTypes.shape({ ...baseSectionShape }))
};

const navPropTypes = PropTypes.arrayOf(
  PropTypes.shape({
    ...sectionShape
  })
);

export default navPropTypes;
