import React from 'react';
import _ from 'lodash';
import AudioClipList from './AudioClipList';
import AudioPlayer from './AudioPlayer';
import Text from '../common/Text';
import blobTheme from '../../audio/blob-theme.mp3';
import gaTheme from '../../audio/ga-theme.mp3';
import stillAlive from '../../audio/still-alive.mp3';
import sunlight from '../../audio/sunlight.mp3';

class AudioMenu extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      clipsData: [
        {
          clip: blobTheme,
          name: 'Blob! Theme'
        },
        {
          clip: gaTheme,
          name: 'Galactic Assault Theme'
        },
        {
          clip: sunlight,
          name: 'Sunlight'
        },
        {
          clip: stillAlive,
          name: 'Still Alive from Portal'
        }
      ],
      // The clip to be loaded by default.
      clip: null
    };

    this.onClickClip = this.onClickClip.bind(this);
  }

  onClickClip(event, clip) {
    this.setState({
      clip: clip
    });
  }

  render() {
    return (
      <div
        className={`audio-menu${
          this.props.className ? ` ${this.props.className}` : ''
        }`}
      >
        <div className="header">
          <AudioPlayer clip={this.state.clip} />
        </div>
        <AudioClipList
          onClickClip={this.onClickClip}
          clipsData={this.state.clipsData}
        />
        <div className="footer">
          <Text className="clip-name">
            {_.get(
              _.find(
                this.state.clipsData,
                clipData => clipData.clip === this.state.clip
              ),
              'name'
            ) || ''}
          </Text>
        </div>
      </div>
    );
  }
}

export default AudioMenu;
