import React from 'react';
import PropTypes from 'prop-types';

const Image = ({ className, style, fillWidth, src, alt, ...props } = props) => {
  return (
    <img
      className={`image${className ? ` ${className}` : ''}${
        fillWidth ? ' full' : ''
      }`}
      style={style}
      src={src}
      alt={alt}
      {...props}
    />
  );
};

Image.defaultProps = {
  alt: 'An image.',
  src: 'http://placehold.it/75'
};

Image.propTypes = {
  /**
   * The source for the image. If this is a file in the project, this must be given as
   * {require(pathRelativeToFile)}.
   */
  src: PropTypes.string.isRequired,
  alt: PropTypes.string
};

export default Image;
