import React from 'react';

/**
 * Component that represents a full-screen element that fades the background to black.
 * Styles should be made such that .fader covers the whole screen, darkens, and blocks clicks
 * to things in the background.
 */
const Fader = ({ ...props } = props) => {
  return (
    <span
      {...props}
      className={`fader${props.className ? ` ${props.className}` : ''}`}
    ></span>
  );
};

export default Fader;
