import React from 'react';
import InfoCard from '../common/InfoCard';
import InfoCardSection from '../common/InfoCardSection';
import Text from '../common/Text';
import DownloadButton from '../common/DownloadButton';
import LinkButton from '../common/LinkButton';
import CardGrid from '../common/CardGrid';
import ScreenshotCard from '../common/ScreenshotCard';
import SupportedDevicesList from '../common/SupportedDevicesList';

const Blob = ({ ...props } = props) => {
  return (
    <InfoCard
      className="info-blob"
      title="Blob!"
      imgSrc={require('../../images/games/blob/blob-screenshot0.jpg')}
      imgAlt="A screenshot of Blob!"
      imgXOffset="-28rem"
      imgYOffset="21rem"
    >
      <SupportedDevicesList icons={['desktop']} />
      <InfoCardSection title="Details">
        <Text>
          Skirt around the screen as an ever-hungry block eating less fortunate
          and sentient blocks. Grow as you eat and use your size--whether big or
          small--to your tactical advantage. Use power-ups to escape hairy
          situations and, most importantly, poop golden sparkles. With every
          level being randomly generated, you'll never have the same level
          twice. As you progress, the obstacles become faster and more numerous.
          A great retro experience, being one of our first games, and running at
          a crisp 640x480 resolution. Neat!
        </Text>
        <Text>Can you win when they run faster than you?</Text>
      </InfoCardSection>
      <InfoCardSection title="Screenshots" noIndent>
        <CardGrid>
          <ScreenshotCard
            imageProps={{
              src: require('../../images/games/blob/blob-screenshot0.jpg')
            }}
          />
        </CardGrid>
      </InfoCardSection>
      <InfoCardSection title="Resources">
        <ul className="horizontal">
          <li>
            <DownloadButton
              src="https://ucg-download.github.io/files/blob.zip"
              name="Blob!"
            />
          </li>
          <li>
            <LinkButton
              to="https://ucg-download.github.io/files/blob_cheats.txt"
              name="Cheats"
            />
          </li>
        </ul>
      </InfoCardSection>
    </InfoCard>
  );
};

export default Blob;
