import React from 'react';
import _ from 'lodash';

/**
 * Represents any kind of simple, paragraph-level text that would be found on the site.
 */
const Text = props => {
  return (
    <p
      className={`text${props.className ? ` ${props.className}` : ''}`}
      style={props.style}
    >
      {props.children}
    </p>
  );
};

export default Text;
