import React from 'react';
import _ from 'lodash';
import PropTypes from 'prop-types';
import Icon from '../common/Icon';
import PlaybackBar from './PlaybackBar';
import { getInteger } from '../../data/util';

class AudioPlayer extends React.Component {
  constructor(props) {
    super(props);

    this.htmlAudioControl = React.createRef();

    this.state = {
      playing: props.autoplay,
      audioControlDetails: {
        currentTime: 0,
        duration: 0
      }
    };

    this.togglePlayback = this.togglePlayback.bind(this);
  }

  componentDidMount() {
    // Exists to update the current playback time.
    this.updateTimer = setInterval(() => {
      this.setState({
        audioControlDetails: this.getAudioControlDetails()
      });
    }, 500);
  }

  componentWillUnmount() {
    clearInterval(this.updateTimer);
  }

  componentDidUpdate(prevProps, prevState) {
    // If the clip changes, automatically start playing the clip.
    if (prevProps.clip !== this.props.clip) {
      this.setState({
        playing: true
      });
    }

    this.manageAudioAccordingToState();
  }

  togglePlayback() {
    this.setState({
      playing: !this.state.playing
    });
  }

  manageAudioAccordingToState() {
    if (this.state.playing) {
      if (this.htmlAudioControl.current.paused) {
        this.htmlAudioControl.current.play();
      }
    } else {
      if (!this.htmlAudioControl.current.paused) {
        this.htmlAudioControl.current.pause();
      }
    }
  }

  getAudioControlDetails() {
    return {
      currentTime: this.htmlAudioControl.current
        ? this.htmlAudioControl.current.currentTime
        : 0,
      duration: this.htmlAudioControl.current
        ? this.htmlAudioControl.current.duration
        : 0
    };
  }

  getTimeDisplay(minValue, currentValue, maxValue) {
    // Kind of unnecessary since we can just assume the min time is 0, but it's here anyway.
    const max = maxValue - minValue;

    const maxTimeMinutes = getInteger(max / 60);
    const maxTimeSeconds = getInteger((max - maxTimeMinutes * 60) % 60);
    const currentTimeMinutes = getInteger(currentValue / 60);
    const currentTimeSeconds = getInteger(
      (currentValue - currentTimeMinutes * 60) % 60
    );

    return `${_.padStart(currentTimeMinutes, 2, '0')}:${_.padStart(
      currentTimeSeconds,
      2,
      '0'
    )}/${_.padStart(maxTimeMinutes, 2, '0')}:${_.padStart(
      maxTimeSeconds,
      2,
      '0'
    )}`;
  }

  render() {
    const { clip, autoplay } = this.props;

    return (
      <div className="audio-player">
        {
          // The audio isn't used at all. It's hidden and the controls are home-brewed by yours truly and managed by JS.
        }
        <audio
          ref={this.htmlAudioControl}
          src={clip}
          type="audio/mpeg"
          className="hidden"
          autoPlay={autoplay}
        />
        <Icon
          solid
          icon={this.state.playing ? 'pause' : 'play'}
          onClick={this.togglePlayback}
          className="playback-toggle"
        />
        <PlaybackBar
          showProgressDisplay
          progressToString={this.getTimeDisplay}
          minValue={0}
          currentValue={this.state.audioControlDetails.currentTime || 0}
          maxValue={this.state.audioControlDetails.duration || 0}
        />
      </div>
    );
  }
}

AudioPlayer.defaultProps = {
  autoplay: false
};

AudioPlayer.propTypes = {
  /**
   * Whether the audio player should start playing on load.
   */
  autoplay: PropTypes.bool,
  /**
   * The clip to play. This would be imported via `import` or `require` and that
   * ref would be passed to the audio player.
   */
  clip: PropTypes.string
};

export default AudioPlayer;
