import React from 'react';
import CardGridItem from './CardGridItem';
import ExpandableImage from './ExpandableImage';

/**
 * A card that's completely taken up by an image. Meant to be use in a `CardGrid`.
 * Clicking on this card will allow the user to see a larger version of the image.
 */
class ScreenshotCard extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      showingFullImage: false
    };

    this.onClickHandler = this.onClickHandler.bind(this);
  }

  onClickHandler(event) {}

  render() {
    return (
      <CardGridItem className="screenshot-card" onClick={this.onClickHandler}>
        <ExpandableImage fillWidth {...this.props.imageProps} />
      </CardGridItem>
    );
  }
}

export default ScreenshotCard;
