import React from 'react';
import Card from './Card';
import Heading from './Heading';
import ChipImage from './ChipImage';

/**
 * Represents a page where the user can go to get more info on something. This page features an image at the top
 * of the card, a title for the card, and then details that serve as the info. The img src and title are given
 * through props, the details are given as children of this component. This is meant to be a large card.
 */
const InfoCard = ({
  title,
  imgSrc,
  imgAlt,
  imgXOffset,
  imgYOffset,
  className,
  children,
  ...props
} = props) => {
  return (
    <Card
      className={`info-card responsive-full-width${
        className ? ` ${className}` : ''
      }`}
      center
    >
      <ChipImage
        className="subject-image center"
        src={imgSrc}
        alt={imgAlt}
        xOffset={imgXOffset}
        yOffset={imgYOffset}
      />
      <Heading className="subject-title" importance="3" center>
        {title}
      </Heading>
      <div className="subject-details">{children}</div>
    </Card>
  );
};

export default InfoCard;
