import React from 'react';
import Icon from './Icon';

const IconButton = ({ href, name, icon, ...props } = props) => {
  return (
    <a className="icon-button" href={href} {...props}>
      <span className="icon-container">
        <Icon solid matchContainer icon={icon} className="center" />
      </span>
      <span className="name-container">{name}</span>
    </a>
  );
};

export default IconButton;
