import React from 'react';
import { Link } from 'react-router-dom';
import Feature from './Feature';
import {
  GALACTIC_ASSAULT,
  CLOCKULATOR,
  ABOUT
} from '../../data/constants/intralinks';

/**
 * The home page for the site.
 */
const HomePage = props => {
  return (
    <div>
      <Feature
        imgSrc={require('../../images/features/ga-screenshot0.jpg')}
        imgAlt="An image of our latest game, Galactic Assault."
        title="Latest Game"
      >
        Our latest game is Galactic Assault, an arcade space shooter with
        procedurally-generated bosses and local multiplayer! Download it{' '}
        <Link to={GALACTIC_ASSAULT}>here</Link>.
      </Feature>
      <Feature
        imgSrc={require('../../images/features/clockulator-screenshot0.jpg')}
        imgAlt="An image of our latest bit of software, the Clockulator."
        title="Software"
      >
        Occasionally we make tools for various reasons. Some of these tools we
        make available to the public. Pictured here is our latest
        time-management tool, The Clockulator. It's a tool for planning out when
        to wake up that runs in your browser, so there's no download necessary.
        Check out the <Link to={CLOCKULATOR}>The Clockulator</Link>.
      </Feature>
      <Feature
        imgSrc={require('../../images/features/about-us.jpg')}
        imgAlt="UCG logo."
        title="About Us"
      >
        We're a very small game development team that just makes games for the
        fun of games. We're not a big deal, but who knows? Maybe you'd like to
        know more <Link to={ABOUT}>about us</Link>.
      </Feature>
    </div>
  );
};

export default HomePage;
