import React from 'react';
import _ from 'lodash';
import { Link } from 'react-router-dom';
import navPropTypes from '../../data/prop-types/nav';
import { HOME } from '../../data/constants/intralinks';
import Icon from '../common/Icon';

class MobileNav extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      expanded: false
    };

    this.onClickHamburgerHandler = this.onClickHamburgerHandler.bind(this);
    this.closeNav = this.closeNav.bind(this);
  }

  closeNav() {
    this.setState({
      expanded: false
    });
  }

  onClickHamburgerHandler() {
    this.setState({
      expanded: !this.state.expanded
    });
  }

  render() {
    return (
      <div className="nav mobile visible-md">
        <div className="logo-container">
          <Link to={HOME} className="no-hover-effects">
            <img
              className="logo"
              src={require('../../images/UCG_Black.png')}
              alt="The logo for Unlucky Cricket Games"
            />
          </Link>
        </div>
        <div
          className="expand-nav-button"
          onClick={this.onClickHamburgerHandler}
        >
          <Icon solid xl icon="bars" />
        </div>
        <nav className={`${this.state.expanded ? 'expanded' : ''}`}>
          {_.map(this.props.nav, navData => (
            <Link
              id={navData.name}
              key={navData.name}
              className={`nav-link${
                navData.link === this.props.location.pathname ? ' active' : ''
              }`}
              to={navData.link}
              onClick={this.closeNav}
            >
              {navData.description.toUpperCase()}
            </Link>
          ))}
        </nav>
      </div>
    );
  }
}

MobileNav.propTypes = {
  nav: navPropTypes
};

export default MobileNav;
