import React from 'react';
import PropTypes from 'prop-types';

/**
 * Leverages font awesome to get icons. An icon type must be specified with attributes.
 * Use solid, regular, light, or duotone to customize the icon.
 */
const Icon = ({
  solid,
  regular,
  light,
  duotone,
  icon,
  matchContainer,
  xs,
  sm,
  md,
  lg,
  xl,
  chip,
  className,
  ...props
} = props) => {
  /**
   * @returns {string} returns the size of the icon as a string based on the props passed in.
   */
  const getIconSize = () => {
    return matchContainer
      ? 'match-container'
      : xs
      ? 'xs'
      : sm
      ? 'sm'
      : md
      ? 'md'
      : lg
      ? 'lg'
      : xl
      ? 'xl'
      : 'md';
  };

  /**
   * Gets the icon prefix based on the props passed in.
   *
   * @returns {string} the appropriate prefix based on the props. If none of prefix props are given,
   * solid is assumed.
   */
  const getIconPrefix = () => {
    return solid
      ? 'fas'
      : regular
      ? 'far'
      : light
      ? 'fal'
      : duotone
      ? 'fad'
      : 'fas';
  };

  return (
    <span
      {...props}
      className={`icon-container${` ${getIconSize()}`}${chip ? ' chip' : ''}${
        className ? ` ${className}` : ''
      }`}
    >
      <i className={`${getIconPrefix()} fa-${icon} icon center`} />
    </span>
  );
};

Icon.propTypes = {
  /**
   * Whether the size of the icon should match its container.
   */
  matchContainer: PropTypes.bool
};

export default Icon;
