import React from 'react';
import InfoCard from '../common/InfoCard';
import InfoCardSection from '../common/InfoCardSection';
import Text from '../common/Text';

const Jack = ({ ...props } = props) => {
  return (
    <InfoCard
      className="info-jack"
      title="Jack"
      imgSrc={require('../../images/about/jack.jpg')}
      imgAlt="A picture of Jack."
      imgXOffset="-12rem"
      imgYOffset="4rem"
    >
      <InfoCardSection title="About">
        <Text>
          Jack is currently studying biology at Hope College. At Hope, he also
          plays on the men's soccer team and is involved in Greek life. Jack
          hopes to go to dental school after graduating from Hope to pursue a
          career in Dentistry.
        </Text>
      </InfoCardSection>
    </InfoCard>
  );
};

export default Jack;
