import React from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import { Redirect } from 'react-router';
import HomePage from './home/HomePage';
import NotFoundPage from './common/NotFoundPage';
import GamesPage from './games/GamesPage';
import SoftwarePage from './software/SoftwarePage';
import AboutPage from './about/AboutPage';
import GalacticAssault from './games/GalacticAssault';
import PopCube from './games/PopCube';
import Blob from './games/Blob';
import Virtuoso from './software/Virtuoso';
import Clockulator from './software/Clockulator';
import Josh from './about/Josh';
import Sam from './about/Sam';
import Jack from './about/Jack';
import Legal from './legal/Legal';
import * as PATHS from '../data/constants/intralinks';
import Nav from './nav/Nav';
import ScrollToTop from './common/ScrollToTop';
import AudioWidget from './audio-widget/AudioWidget';
import SiteFooter from './common/SiteFooter';

/**
 * Represents the top-most component of the site. Everything is displayed here via a BrowserRouter.
 * This element handles all routing.
 *
 * This web site made from scratch with <3 by Josh Truitt, powered by React.
 *
 * @param {object} props
 */
const App = props => {
  return (
    <Router>
      <ScrollToTop>
        <Nav location={location} />
        <AudioWidget />
        <Switch>
          <Route exact path="/" component={HomePage} />
          <Route
            exact
            path="/home"
            render={() => <Redirect to={PATHS.HOME} />}
          />
          <Route
            exact
            path="/index.html"
            render={() => <Redirect to={PATHS.HOME} />}
          />

          <Route exact path={PATHS.GAMES} component={GamesPage} />
          <Route
            exact
            path={PATHS.GALACTIC_ASSAULT}
            component={GalacticAssault}
          />
          <Route exact path={PATHS.POP_CUBE} component={PopCube} />
          <Route exact path={PATHS.BLOB} component={Blob} />

          <Route exact path={PATHS.SOFTWARE} component={SoftwarePage} />
          <Route exact path={PATHS.VIRTUOSO} component={Virtuoso} />
          <Route exact path={PATHS.CLOCKULATOR} component={Clockulator} />

          <Route exact path={PATHS.ABOUT} component={AboutPage} />
          <Route exact path={PATHS.JOSH} component={Josh} />
          <Route exact path={PATHS.SAM} component={Sam} />
          <Route exact path={PATHS.JACK} component={Jack} />

          <Route exact path={PATHS.LEGAL} component={Legal} />

          <Route component={NotFoundPage} />
        </Switch>
        <SiteFooter />
      </ScrollToTop>
    </Router>
  );
};

export default App;
