import React from 'react';
import InfoCard from '../common/InfoCard';
import InfoCardSection from '../common/InfoCardSection';
import Text from '../common/Text';
import DownloadButton from '../common/DownloadButton';
import CardGrid from '../common/CardGrid';
import ScreenshotCard from '../common/ScreenshotCard';
import SupportedDevicesList from '../common/SupportedDevicesList';

const GalacticAssault = ({ ...props } = props) => {
  return (
    <InfoCard
      className="info-galactic-assault"
      title="Galactic Assault"
      imgSrc={require('../../images/games/galactic-assault/ga-screenshot0.jpg')}
      imgAlt="A screenshot of Galactic Assault"
      imgXOffset="-66rem"
      imgYOffset="38rem"
    >
      <SupportedDevicesList icons={['desktop']} />
      <InfoCardSection title="Details">
        <Text>
          Our latest game, Galactic Assault is a top-down arcade space shooter
          available for Windows. It features couch co-op if you've got gamepads
          handy, endless play, and a whole lot of battle. Face off against
          procedurally generated bosses at milestone levels so you'll never have
          the same challenge twice. How far can you get?
        </Text>
      </InfoCardSection>
      <InfoCardSection title="Screenshots" noIndent>
        <CardGrid>
          <ScreenshotCard
            imageProps={{
              src: require('../../images/games/galactic-assault/ga-screenshot0.jpg')
            }}
          />
        </CardGrid>
      </InfoCardSection>
      <InfoCardSection className="hidden-sm" title="Resources">
        <DownloadButton
          src="https://ucg-download.github.io/files/ga_build0.2.0.zip"
          name="Galactic Assault"
        />
      </InfoCardSection>
    </InfoCard>
  );
};

export default GalacticAssault;
