import React from 'react';
import PropTypes from 'prop-types';
import * as LINKS from '../../data/constants/intralinks';
import DesktopNav from './DesktopNav';
import MobileNav from './MobileNav';

const Nav = ({ location, ...props } = props) => {
  const nav = [
    {
      name: 'GAMES',
      description: 'Games',
      link: LINKS.GAMES,
      subSections: [
        {
          name: 'GA',
          description: 'Galactic Assault',
          link: LINKS.GALACTIC_ASSAULT
        },
        {
          name: 'POP_CUBE',
          description: 'Pop! Cube',
          link: LINKS.POP_CUBE
        },
        {
          name: 'BLOB',
          description: 'Blob!',
          link: LINKS.BLOB
        }
      ]
    },
    {
      name: 'SOFTWARE',
      description: 'Software',
      link: LINKS.SOFTWARE,
      subSections: [
        {
          name: 'CLOCKULATOR',
          description: 'Clockulator',
          link: LINKS.CLOCKULATOR
        },
        {
          name: 'VIRTUOSO',
          description: 'Virtuoso',
          link: LINKS.VIRTUOSO
        }
      ]
    },
    {
      name: 'ABOUT',
      description: 'About Us',
      link: LINKS.ABOUT
    },
    {
      name: 'LEGAL',
      description: 'Legal',
      link: LINKS.LEGAL
    }
  ];

  return (
    <>
      <DesktopNav nav={nav} location={location} />
      <MobileNav nav={nav} location={location} />
    </>
  );
};

Nav.propTypes = {
  /**
   * Received from the App component via the Router that exists there.
   */
  location: PropTypes.object.isRequired
};

export default Nav;
