import React from 'react';
import _ from 'lodash';
import { clipsData } from '../../data/prop-types/audioWidget';
import AudioClipListItem from './AudioClipListItem';

const AudioClipList = ({ onClickClip, clipsData, ...props } = props) => {
  return (
    <ul className="audio-clip-list">
      {_.map(clipsData, clipData => (
        <AudioClipListItem
          clip={clipData.clip}
          clipName={clipData.name}
          key={clipData.name}
          onClick={e => onClickClip(e, clipData.clip)}
        />
      ))}
    </ul>
  );
};

AudioClipList.propTypes = {
  clipsData: clipsData
};

export default AudioClipList;
