import React from 'react';
import Icon from '../common/Icon';
import Fader from '../common/Fader';
import AudioMenu from './AudioMenu';

class AudioWidget extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      expanded: false,
      audioClip: ''
    };

    this.openHandler = this.openHandler.bind(this);
    this.closeHandler = this.closeHandler.bind(this);
  }

  openHandler() {
    this.setState({
      expanded: true
    });
  }

  closeHandler() {
    this.setState({
      expanded: false
    });
  }

  render() {
    return (
      <div
        className={`audio-widget hidden-lg${
          this.state.expanded ? ' expanded' : ''
        }`}
      >
        <div className="open-menu-button">
          <Icon onClick={this.openHandler} icon="volume-off" solid />
        </div>
        <Icon
          solid
          icon="times"
          className="full-screen-close-button"
          onClick={this.closeHandler}
          xl
        />

        <Fader
          className={`${this.state.expanded ? '' : 'hidden'}`}
          onClick={this.closeHandler}
        />
        <AudioMenu
          className={`${this.state.expanded ? '' : 'hidden'}`}
          clip={this.state.audioClip}
        />
      </div>
    );
  }
}

export default AudioWidget;
