import React from 'react';
import Image from './Image';
import Fader from './Fader';
import Icon from './Icon';

/**
 * Represents an `Image` that can be clicked on to make it bigger for viewing.
 */
class ExpandableImage extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      expanded: false
    };

    this.onClickHandler = this.onClickHandler.bind(this);
    this.onCloseHandler = this.onCloseHandler.bind(this);
  }

  onClickHandler() {
    this.setState({ expanded: !this.state.expanded });
  }

  onCloseHandler() {
    this.setState({
      expanded: false
    });
  }

  render() {
    return (
      <>
        {this.state.expanded && (
          <>
            <Icon
              solid
              icon="times"
              className="full-screen-close-button"
              onClick={this.onCloseHandler}
              xl
            />
            <Fader onClick={this.onCloseHandler} />
            <Image
              {...this.props}
              onClick={this.onClickHandler}
              className={`expandable expanded${
                this.props.className ? ` ${this.props.className}` : ''
              }`}
            />
          </>
        )}
        <Image
          {...this.props}
          onClick={this.onClickHandler}
          className={`expandable${
            this.props.className ? ` ${this.props.className}` : ''
          }`}
        />
      </>
    );
  }
}

export default ExpandableImage;
