import React from 'react';
import CardGrid from '../common/CardGrid';
import ItemSummaryCard from '../common/ItemSummaryCard';
import { VIRTUOSO, CLOCKULATOR } from '../../data/constants/intralinks';

/**
 * The home page for the /games route.
 */
const SoftwarePage = props => {
  return (
    <div>
      <CardGrid>
        <ItemSummaryCard
          imgSrc={require('../../images/software/virtuoso/virtuoso-icon.ico')}
          imgAlt="Virtuoso's Icon"
          title="Virtuoso"
          description="A console application that turns plain text into .WAV files, 
            allowing you to make the music you want."
          linkTo={VIRTUOSO}
        />
        <ItemSummaryCard
          imgSrc={require('../../images/software/clockulator/clockulator-software-page.jpg')}
          imgAlt="Screenshot of The Clockulator"
          title="The Clockulator"
          description="A web app that lets you calculate how long you can sleep in."
          linkTo={CLOCKULATOR}
        />
      </CardGrid>
    </div>
  );
};

export default SoftwarePage;
