import React from 'react';
import PropTypes from 'prop-types';

/**
 * Represents a heading. Include an importance attribute of 1-6 for how important heading is and include some special
 * styling for that heading. 1 is most important, 6 is least. Default is 1.
 */
const Heading = ({ importance, center, ...props } = props) => {
  return (
    <h2
      className={`heading imp${importance}${
        props.className ? ` ${props.className}` : ''
      }${center ? ' text-center' : ''}`}
      style={props.style}
    >
      {props.children}
    </h2>
  );
};

Heading.defaultProps = {
  importance: '1'
};

Heading.propTypes = {
  importance: PropTypes.string,
  /**
   * Centers the text.
   */
  center: PropTypes.bool
};

export default Heading;
