import React from 'react';
import Image from './Image';

const ChipImage = ({ src, alt, xOffset, yOffset, style, ...props } = props) => {
  const imgStyle = {
    bottom: yOffset || 0,
    left: xOffset || 0
  };

  return (
    // Two containers to allow a white border around the image. The outer container manages sizing the image area,
    // the second container pushes the image viewport in and stops the overflow from going into the upper container
    // (maintaining a strip of background).
    <div
      className={`chip-image-container light-shadowed${
        props.className ? ` ${props.className}` : ''
      }`}
      style={{
        ...style
      }}
    >
      <Image className="chip-image" src={src} alt={alt} style={imgStyle} />
    </div>
  );
};

export default ChipImage;
