import React from 'react';
import PropTypes from 'prop-types';

const HorizontalRule = ({ uncover, ...props } = props) => (
  <div
    className={`horizontal-rule center${
      props.className ? ` ${props.className}` : ''
    }${uncover ? ' anim-uncover' : ''}`}
  />
);

HorizontalRule.defaultProps = {
  uncover: false
};

HorizontalRule.propTypes = {
  /**
   * Whether the horizontal rule should do an uncovering animation on load.
   */
  uncover: PropTypes.bool
};

export default HorizontalRule;
