import React from 'react';
import IconButton from './IconButton';

/**
 * A component that represents a button to be pressed that links the user to something. The
 * link is opened in a new tab. This shouldn't be used for intrasite navigation.
 */
const LinkButton = ({ to, name, ...props } = props) => {
  return (
    <IconButton
      href={to}
      name={name}
      icon="external-link-alt"
      target="_blank"
    />
  );
};

export default LinkButton;
