import React from 'react';
import PropTypes from 'prop-types';

/**
 * An item in a list of audio clips.
 */
const AudioClipListItem = ({ clipName, ...props } = props) => {
  return (
    <li {...props} className="audio-clip-list-item">
      {clipName}
    </li>
  );
};

AudioClipListItem.propTypes = {
  /**
   * The name of the clip, as it will presented to the user.
   */
  clipName: PropTypes.string
};

export default AudioClipListItem;
