import React from 'react';
import PropTypes from 'prop-types';
import Heading from './Heading';

/**
 * Component that represents a section in an InfoCard. Can be given a title prop for a simple heading
 * or a heading prop for a component as the heading. These props are mutually exclusive. If both are
 * given, the title will be wiped and only the passed heading will render. Use the children as the contents
 * of the section.
 */
const InfoCardSection = ({
  title,
  heading,
  children,
  className,
  noIndent,
  ...props
} = props) => {
  if (title && heading) {
    title = null;
  }

  return (
    <div className={`info-card-section${className ? ` ${className}` : ''}`}>
      <div className="info-card-section-heading-container">
        {title ? <Heading importance="5">{title}</Heading> : heading}
      </div>
      <div
        className={`info-card-section-content-container${
          !noIndent ? ' indent md' : ''
        }`}
      >
        {children}
      </div>
    </div>
  );
};

InfoCardSection.propTypes = {
  /**
   * The title of the section. Will be created as a Heading component with importance 5. If you use this prop,
   * don't use the heading prop.
   */
  title: PropTypes.string,
  /**
   * A heading component to be used as the heading for the section. Allows more customization than
   * just using the title prop. If you use this prop, don't use the title prop.
   */
  heading: PropTypes.object
};

export default InfoCardSection;
