import React from 'react';
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';
import ChipImage from '../common/ChipImage';
import Heading from '../common/Heading';

/**
 * Represents a portrait and nameplate for someone. Also includes the possibility to include a
 * link to a page that contains more info on the person.
 */
const Portrait = ({
  name,
  linkToMoreInfo,
  chipImageProps,
  imageProps,
  className,
  ...props
} = props) => {
  const defaultRender = (
    <div>
      <ChipImage {...chipImageProps} {...imageProps} />
      <div className="nameplate">
        <Heading importance="4" center>
          {name}
        </Heading>
      </div>
    </div>
  );

  return (
    <div
      className={`portrait-container${linkToMoreInfo ? ' has-more-info' : ''}${
        className ? ` ${className}` : ''
      }`}
    >
      {linkToMoreInfo ? (
        <Link className="plain-link" to={linkToMoreInfo}>
          {defaultRender}
        </Link>
      ) : (
        defaultRender
      )}
    </div>
  );
};

Portrait.propTypes = {
  /**
   * The name of the person the portrait is of.
   */
  name: PropTypes.string.isRequired,
  /**
   * An internal link to a page that gives more info for the person. This is not required.
   */
  linkToMoreInfo: PropTypes.string
};

export default Portrait;
