import React from 'react';
import _ from 'lodash';
import PropTypes from 'prop-types';
import Text from '../common/Text';
import {
  color_background,
  color_darkenedBackground
} from '../../styles/_global-vars.scss';

const PlaybackBar = ({
  minValue,
  currentValue,
  maxValue,
  showProgressDisplay,
  progressToString,
  ...props
} = props) => {
  return (
    <div className="progress-bar">
      <input
        className="slider"
        type="range"
        min={minValue}
        value={currentValue}
        max={maxValue}
        style={{
          background: `linear-gradient(90deg, ${color_background} ${progressAsPercent(
            minValue,
            currentValue,
            maxValue
          )}%, ${color_darkenedBackground} ${progressAsPercent(
            minValue,
            currentValue,
            maxValue
          ) + 0.1}%)`
        }}
      />
      {showProgressDisplay && (
        <Text className="progress-display">
          {progressToString(minValue, currentValue, maxValue)}
        </Text>
      )}
    </div>
  );
};

function progressAsPercent(minValue, currentValue, maxValue) {
  return +(currentValue / (maxValue - minValue)) * 100;
}

function defaultProgressToStringImplementation(
  minValue,
  currentValue,
  maxValue
) {
  const percent = progressAsPercent(minValue, currentValue, maxValue);

  return _.isNil(percent) || _.isNaN(percent) ? '' : `${percent.toFixed(0)}%`;
}

PlaybackBar.defaultProps = {
  progressToString: defaultProgressToStringImplementation
};

PlaybackBar.propTypes = {
  /**
   * The minimum value this progress bar can have.
   */
  minValue: PropTypes.number,
  /**
   * The current value of the fill of the progress bar.
   */
  currentValue: PropTypes.number,
  /**
   * The maxmimum value this progress bar can have.
   */
  maxValue: PropTypes.number,
  /**
   * Whether the progress display for the progress bar shows. How the progress displays is dependent on
   * the implementation of the progressToString function prop, which has a default implementation
   * of showing the progress as a percent.
   */
  showProgressDisplay: PropTypes.bool,
  /**
   * A function that describes how the progress is represented in string form. This function takes
   * a minValue, a currentValue, and a maxValue, and returns a string representing the progress
   * as a string.
   */
  progressToString: PropTypes.func
};

export default PlaybackBar;
