import React from 'react';
import InfoCard from '../common/InfoCard';
import InfoCardSection from '../common/InfoCardSection';
import Text from '../common/Text';

const Sam = ({ ...props } = props) => {
  return (
    <InfoCard
      className="info-sam"
      title="Sam"
      imgSrc={require('../../images/about/sam.jpg')}
      imgAlt="A picture of Sam."
      imgXOffset="-12rem"
      imgYOffset="2rem"
    >
      <InfoCardSection title="About">
        <Text>
          Sam helped me out with the theme song for Blob!, which was a ton of
          fun to make.
        </Text>
        <Text>
          Sam graduated from the University of Michigan with a degree in BSE in
          Aerospace Engineering and a minor in physics. He currently works full
          time as a systems engineer for Northrop Grumman and part-time on
          personal game development projects, with the first game release
          tentatively set for Christmas 2019.
        </Text>
      </InfoCardSection>
    </InfoCard>
  );
};

export default Sam;
