import React from 'react';
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';
import Image from './Image';
import Heading from './Heading';
import Text from './Text';
import CardGridItem from './CardGridItem';

/**
 * Represents a small card for an item. The item could be anything that warrants clicking on the card to get more
 * information. The card serves as a way to showcase the item with an image and a short description about it. The user
 * should be able to click on the card to be taken to a page with more information about the item.
 */
const ItemSummaryCard = ({
  imgSrc,
  imgAlt,
  imgXOffset,
  imgYOffset,
  title,
  description,
  linkTo,
  ...props
} = props) => {
  const imgStyle = {
    bottom: imgYOffset || 0,
    left: imgXOffset || 0
  };

  const headingStyle = {
    margin: '0'
  };

  return (
    <CardGridItem className="product-card responsive-full-width">
      <Link className="plain-link" to={linkTo}>
        <div className="image-window">
          <Image src={imgSrc} alt={imgAlt} style={imgStyle} />
        </div>
        <div className="product-details-window">
          <Heading
            className="product-title text-caps"
            style={headingStyle}
            importance="5"
          >
            {title}
          </Heading>
          <Text className="product-description">{description}</Text>
        </div>
      </Link>
    </CardGridItem>
  );
};

ItemSummaryCard.propTypes = {
  /**
   * The source of the product image.
   */
  imgSrc: PropTypes.string.isRequired,
  /**
   * The alt for the image.
   */
  imgAlt: PropTypes.string.isRequired,
  /**
   * The X offset to use for the image, in the event the card doesn't show the part of
   * the image you want to show. This will be in the units you specify. A positive number
   * moves the image right, a negative number moves the image left.
   */
  imgXOffset: PropTypes.string,
  /**
   * The Y offset to use for the image, in the event the card doesn't show the part of
   * the image you want to show. This will be in the units you specify. A positive number
   * moves the image up, a negative number moves the image down.
   */
  imgYOffset: PropTypes.string,
  /**
   * The product name.
   */
  title: PropTypes.string.isRequired,
  /**
   * A short description for the product.
   */
  description: PropTypes.string.isRequired,
  /**
   * The link the user gets sent to if they click on the product card.
   */
  linkTo: PropTypes.string.isRequired
};

export default ItemSummaryCard;
