import React from 'react';
import PropTypes from 'prop-types';

const Card = ({ fillWidth, width, center, ...props } = props) => {
  return (
    <div
      style={{ width, ...props.style }}
      className={`card${fillWidth ? ' full' : ''}${
        props.className ? ` ${props.className}` : ''
      }${center ? ' center' : ''}`}
    >
      {props.children}
    </div>
  );
};

Card.defaultProps = {
  fillWidth: false
};

Card.propTypes = {
  /**
   * Whether the card should fill the full width of the screen.
   */
  fillWidth: PropTypes.bool,
  /**
   * Whether the card has a shadow.
   */
  shadowed: PropTypes.bool,
  /**
   * The width, in whatever unit is specified.
   */
  width: PropTypes.string
};

export default Card;
