import React from 'react';
import { Link } from 'react-router-dom';
import InfoCard from '../common/InfoCard';
import InfoCardSection from '../common/InfoCardSection';
import Text from '../common/Text';
import { GALACTIC_ASSAULT, POP_CUBE } from '../../data/constants/intralinks';

const Josh = ({ ...props } = props) => {
  return (
    <InfoCard
      className="info-josh"
      title="Josh"
      imgSrc={require('../../images/about/josh.jpg')}
      imgAlt="A picture of Josh, founder of Unlucky Cricket Games."
      imgXOffset="-11rem"
      imgYOffset="9rem"
    >
      <InfoCardSection title="About">
        <Text>
          Hey there, my name's Josh. I'm the founder of Unlucky Cricket Games
          and I do all kinds of stuff for it. I wrote this website from scratch
          using React, I've made several games with several tools, and I've also
          spent some time developing personal-use software tools (some of which
          I've made available to you through this site). I started making games
          when I was 12 years old with a crummy little piece of software called
          HyperStudio which is essentially PowerPoint on some heavy steroids.
          The steroids involved come from HyperLogo, the scripting language for
          the program. I almost finished a full game in HyperStudio that I'd
          made a board-game of beforehand but, being the crummy software it was,
          the project became incredibly unstable and impossible to finish. Only
          at that point did I notice that the creator had a small note that said
          projects over a certain file size would be unstable. Sure enough, I
          had more than exceeded that limit.
        </Text>
        <Text>
          After that terrible debacle and sadness of losing a couple months of
          work, I started dabbling in "real" languages and tools. I started with
          BlitzBasic which, while writing this, I tried to find the website for
          so I could link to it, but the site seems to have been taken down and
          now the software and language are free.
        </Text>
        <Text>Oof.</Text>
        <Text>
          Anyway, I made a couple games in that, like the original Galactic
          Assault (the one available on the site is an off-shoot I made with{' '}
          <a href="https://unity.com/">Unity</a>), and Blob!. After getting
          frustrated with how limiting the tool was, I moved on to{' '}
          <a href="https://unity.com/">Unity</a>, where I've stayed since. It's
          a great tool and my go-to for game development. I made both{' '}
          <Link to={POP_CUBE}>Pop! Cube</Link> and{' '}
          <Link to={GALACTIC_ASSAULT}>Galactic Assault</Link> with the tool, as
          well as several other games I started but never finished.
        </Text>
        <Text>
          I hold a BA in French, but I write code for a living because c'est la
          vie. I'm alright with it, though, French and Computer Science were my
          two passions going into higher education, and I <em>did</em> minor in
          Computer Science. It's a lot of fun for me, and I'm proficient in C#
          and JavaScript (ES5 and ES6), and I'm familiar with Java and Groovy.
          And, of course, I can whip up HTML and CSS (or we wouldn't be here).
          I've dabbled in a few other languages, but they're not worth listing.
        </Text>
        <Text>
          At any rate, I hope you enjoy the site and the products I've made with
          the help of some friends!
        </Text>
      </InfoCardSection>
    </InfoCard>
  );
};

export default Josh;
