import React from 'react';
import _ from 'lodash';
import { getUuid } from '../../data/util';
import FlexGrid from '../common/FlexGrid';
import FlexGridItem from '../common/FlexGridItem';
import Portrait from './Portrait';
import Card from '../common/Card';
import Heading from '../common/Heading';
import { JOSH, SAM, JACK } from '../../data/constants/intralinks';

/**
 * The page that's shown for knowing more about Unlucky Cricket games.
 *
 * @param {object} props
 */
const AboutPage = props => {
  const portraitData = {
    josh: {
      name: 'Josh',
      imgXOffset: '-6rem',
      imgYOffset: '2rem',
      linkToMoreInfo: JOSH
    },
    devyn: {
      name: 'Devyn',
      imgXOffset: '0rem',
      imgYOffset: '5rem'
    },
    sam: {
      name: 'Sam',
      imgXOffset: '-7rem',
      imgYOffset: '0rem',
      linkToMoreInfo: SAM
    },
    jack: {
      name: 'Jack',
      imgXOffset: '-7rem',
      imgYOffset: '0rem',
      linkToMoreInfo: JACK
    },
    Karl: {
      name: 'Karl',
      imgXOffset: '-5rem',
      imgYOffset: '0rem'
    }
  };

  return (
    <Card className="responsive-full-width about-page-card" center>
      <Heading className="page-title" center importance="3">
        Meet the Team
      </Heading>
      <FlexGrid className="portrait-grid">
        {_.map(portraitData, data => (
          <FlexGridItem
            className="portrait-grid-item managed-size"
            key={getUuid()}
          >
            <Portrait
              name={data.name}
              chipImageProps={{
                xOffset: data.imgXOffset,
                yOffset: data.imgYOffset
              }}
              imageProps={{
                src: require(`../../images/about/${_.toLower(data.name)}.jpg`),
                alt: data.name
              }}
              linkToMoreInfo={data.linkToMoreInfo}
            />
          </FlexGridItem>
        ))}
      </FlexGrid>
    </Card>
  );
};

export default AboutPage;
