import React from 'react';
import CardGrid from '../common/CardGrid';
import ItemSummaryCard from '../common/ItemSummaryCard';
import {
  GALACTIC_ASSAULT,
  POP_CUBE,
  BLOB
} from '../../data/constants/intralinks';

/**
 * The home page for the /games route.
 */
const GamesPage = props => {
  return (
    <div>
      <CardGrid>
        <ItemSummaryCard
          imgSrc={require('../../images/games/galactic-assault/ga-games-page.jpg')}
          imgAlt="Screenshot of Galactic Assault"
          title="Galactic Assault"
          description="A top-down 2D space shooter with procedurally-generated bosses, local co-op and full gamepad support."
          linkTo={GALACTIC_ASSAULT}
        />
        <ItemSummaryCard
          imgSrc={require('../../images/games/pop-cube/pop-cube-games-page.jpg')}
          imgAlt="Screenshot of Pop! Cube"
          title="Pop! Cube"
          description="An arcade-style game about precision and speed."
          linkTo={POP_CUBE}
        />
        <ItemSummaryCard
          imgSrc={require('../../images/games/blob/blob-games-page.jpg')}
          imgAlt="Screenshot of Blob!"
          title="Blob!"
          description="An arcade-style game about eating squares."
          linkTo={BLOB}
        />
      </CardGrid>
    </div>
  );
};

export default GamesPage;
