import _ from 'lodash';
import React from 'react';
import PropTypes from 'prop-types';
import Icon from './Icon';
import { getUuid } from '../../data/util';

/**
 * A list of icons that represent supported devices.
 */
const SupportedDevicesList = ({ icons, ...props } = props) => {
  return (
    <ul className="supported-devices-list horizontal no-bullets center-items">
      <li>
        {_.map(icons, iconName => (
          <Icon solid lg icon={iconName} key={getUuid()} />
        ))}
      </li>
    </ul>
  );
};

SupportedDevicesList.propTypes = {
  icons: PropTypes.arrayOf(PropTypes.string)
};

export default SupportedDevicesList;
